@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
html {
	font-size: 10px;
}
* {
	box-sizing: border-box;
}
a {
	text-decoration: none;
}
body {
	margin: 0;
	font-family: 'Montserrat', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
		'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	line-height: 1.5;
}

.App {
	min-height: 100vh;
	max-width: 1170px;
	margin: 0 auto;
}

.button{display:inline-flex;align-items:center;justify-content:center;grid-gap:5px;gap:5px;font-size:1.6rem;font-weight:700;height:40px;padding:5px 15px;border-radius:100px;cursor:pointer;-webkit-user-select:none;-ms-user-select:none;user-select:none;white-space:nowrap;transition:all 0.12s}.button_fill{color:#fff;border:2px solid #ff572d;background-color:#ff572d}.button_fill:hover{color:#ff572d;background-color:transparent}.button_outline{color:#ff572d;border:2px solid #ff572d;background-color:transparent}.button_outline:hover{color:#fff;background-color:#ff572d}.form-block{display:flex;flex-direction:column;margin:0 auto;width:450px;padding:50px 5px 30px}.total-text{padding:5px 25px;font-size:2.2rem;font-weight:600}.total-price{padding:0 10px;font-size:2.8rem;font-weight:700;color:#ff572d;white-space:nowrap}.total-old-price{color:#e03b26;text-decoration:line-through;white-space:nowrap}.form-cart{display:flex;flex-direction:column;align-items:center;grid-gap:15px;gap:15px}.discounts{font-size:1.6rem}.devilery-desc{padding-top:20px}input{width:100%;font-size:1.8rem;height:40px;padding:5px 20px;border:none;outline:none;border-radius:20px;font-family:'Montserrat', sans-serif}input:focus{outline:none}@media (max-width: 550px){.form-block{width:100%}}.product__list{display:flex;flex-wrap:wrap;grid-gap:20px;gap:20px}.product{display:flex;flex-direction:column;justify-content:space-between;overflow:hidden;border-radius:20px;flex:0 0 23.5%;background-color:#fff;box-shadow:0px 2px 5px rgba(0,0,0,0.1)}.product .product__content{display:flex;flex-direction:column;justify-content:space-between;padding:20px}.product .product__img{width:100%;height:200px;padding:5px;object-fit:cover}.product .product__name{text-align:center;font-size:1.6rem}.product .product__price{font-weight:700}.main__cart{display:flex;flex-direction:column;align-items:center}.product__cart{display:flex;flex-wrap:wrap;justify-content:space-between;align-items:center;overflow:hidden;width:100%;padding:12px 20px;grid-gap:15px;gap:15px;border-radius:20px;background-color:#fff;box-shadow:0px 2px 5px rgba(0,0,0,0.1)}.product__cart .product__content{display:flex;flex-direction:column;justify-content:space-between;align-items:center;grid-gap:20px;gap:20px;padding:20px}.product__cart .product__control{display:flex;grid-gap:5px;gap:5px}.product__cart .product__img{width:200px;padding:5px;object-fit:cover}.product__cart .product__name{text-align:center;font-size:1.6rem}.product__cart .product__price{white-space:nowrap;font-weight:700}@media (max-width: 1050px){.product{flex:0 0 31.5%}}@media (max-width: 800px){.product{flex:0 1 48.5%}}@media (max-width: 750px){.product{flex:0 1 100%}.product__cart{justify-content:space-around}}.header{display:flex;justify-content:space-between;align-items:center;padding:20px;grid-gap:10px;gap:10px;border-radius:20px;box-shadow:0px 2px 5px rgba(0,0,0,0.22);background-color:#fbf6f6}.header .logo{display:flex;align-items:center;grid-gap:15px;gap:15px}.header .logo .logo__name{font-size:3rem;font-weight:900;font-style:italic;color:#ff572d}.header .logo .logo__img{width:70px}.header .menu-item{font-size:2rem;font-weight:700;text-decoration:none;color:#151515}.header .cart{display:flex;grid-gap:15px;gap:15px}.header .cart .cart__img{height:100%}h2{font-size:3rem}body{background-color:#d4511d;color:#151515}.wrapper{min-height:100vh;display:flex;flex-direction:column;justify-content:space-between;grid-gap:15px;gap:15px;padding:15px 7px}.main{display:flex;flex-direction:column;flex-wrap:wrap;flex:1 0 100%;grid-gap:20px;gap:20px;justify-content:flex-start;align-items:flex-start;padding:20px;border-radius:20px;box-shadow:0px 2px 5px rgba(0,0,0,0.22);background-color:#fbf6f6}.cart__content{flex-direction:column}

